
import { defineComponent } from 'vue'; // 타입스크립트에서 꼭 필요한 부분
import QuizList from '@/assets/quizlist.json'; // typescript 에서는 json 파일 사용하기 위해 tsconfig.json 파일에 "resolveJsonModule": true 추가 필요
import { RouteLocationRaw } from 'vue-router';

interface Quiz {
  id: number,
  question: string,
  answer: string,
  view: {
    number1: string;
    number2: string;
    number3: string;
    number4: string;
  },
  type: string,
  hint: string
}

export default defineComponent({
  name: 'HomeView',
  data() {
    return {
      errorMsg: '이름을 입력해주세요',
      userName: '',
      dataList: [] as Quiz[],
      selectRandom: "0",
      selectType: "전체",
      selectCount : 1
    }
  },
  components: {
  },
  methods: {
    koreanchk() {
      const regex = /^[가-힣]*$/
      if(!regex.test(this.userName)) {
        this.userName = this.userName.replace(/[^가-힣]*/, '')
      }
    },
    /*  :string | undefined */
    NameChk() {
      if(!this.userName){
        const errorEl = document.querySelector('.error')

        errorEl?.classList.remove('invisible', 'opacity-0', 'top-[48%]')
        errorEl?.classList.add('top-1/2', 'opacity-1')
        setTimeout(() => {
          errorEl?.classList.remove('top-1/2', 'opacity-1')
          errorEl?.classList.add('invisible', 'opacity-0', 'top-[48%]')
        }, 2500)
      }else{
        const route : RouteLocationRaw = {
          name : "QuizView",
          query: {
            userName: this.userName,
            selectRandom : this.selectRandom,
            selectType: this.selectType,
            selectCount:this.selectCount
          },
          replace: false // true 이전페이지 지우기(뒤로가기 비활성화)
        }
         this.$router.push(route)
      }
    }
  },
  computed:{
    // 특정 속성을 추출해서 새로운 배열을 만들기 위해 Set 객체를 사용한다. > 중복된 값을 허용하지 않는 데이터 집합 > 새로운 배열로 다시 반환 
    // 우리가 원하는 배열에서 중복값을 제거하고 유일한 값을 배열로 생성
    uniqueTypes(): string[]{
      return [...new Set(this.dataList.map((e)=> e.type))]
    },
    cateCount() :number[]{
      return this.uniqueTypes.map((e)=>{
        return this.dataList.filter((item)=>{
          return e === item.type
        }).length
      })
    },
    cateList() : Quiz[]{
      return this.dataList.filter((e)=>{
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{
          return e.type
        }
      })
    }
  },
  created(){
    this.dataList = QuizList.QuizList as Quiz [];
  },
  mounted() {
    document.querySelector("input")?.focus();
  },
})
